html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  display: grid;
  margin: 0;
  padding: 0;
  font-family: "DM Sans", "Josefin Sans", sans-serif;
  background-color: rgb(131, 131, 131);
}
@keyframes animation {
  0% {
    left: 0;
  }
  100% {
    left: 100%;
  }
}

.logo__img {
  display: block;
  max-width: 200px;
  max-height: 200px;
  object-fit: contain;
  border-radius: 50%;
  margin: 0 auto;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}