@use "../util" as util;
html {
  box-sizing: border-box;
  font-size: 100%;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
body {
  display: grid;
  margin: 0;
  padding: 0;
  font-family: "DM Sans", "Josefin Sans", sans-serif;
  background-color: util.$black;

  @keyframes animation {
    0% {
      left: 0;
    }
    100% {
      left: 100%;
    }
  }
}
.logo__img {
  display: block;
  max-width: 200px;
  max-height: 200px;
  object-fit: contain;
  border-radius: 50%;
  margin: 0 auto;
}
